.design {
    position: relative;
    width: 100%;
    height: 758px;
    background-size: cover;
    background-position: center;
    @include r(1400) {
        height: 54.1428vw; }
    @include r(500) {
        height: 152vw; } }
.design:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.94); }

.design-border {
    position: absolute;
    top: 90px;
    bottom: 90px;
    left: 180px;
    right: 180px;
    border: 1px solid $olive;
    display: flex;
    align-items: center;
    @include r(1400) {
        top: 6.4285vw;
        left: 12.8571vw;
        bottom: 6.4285vw;
        right: 12.8571vw; } }

.design-content {
    position: relative;
    padding-left: 320px;
    @include r(1400) {
        padding-left: 22.8571vw; }
    @include r(500) {
        padding: 0;
        width: 100%; }
    &__title {
        color: $olive;
        font-family: $proxima-bold;
        font-size: 48px;
        text-transform: uppercase;
        width: 500px;
        margin-bottom: 30px;
        @include r(1400) {
            font-size: 3.4285vw;
            width: 35.7142vw;
            margin-bottom: 2.1428vw; }
        @include r(500) {
            font-size: 7vw;
            text-align: center;
            width: 80%;
            margin: auto;
            margin-bottom: 6vw; } }
    &__description {
        color: $white;
        width: 410px;
        line-height: 25px;
        margin-bottom: 30px;
        @include r(1400) {
            width: 50%;
            line-height: 1.7857vw;
            margin-bottom: 2.1428vw; }
        @include r(500) {
            line-height: 5vw;
            font-size: 3vw;
            text-align: center;
            margin: auto;
            margin-bottom: 8vw; } }
    &__btn {
        display: inline-block;
        position: relative;
        z-index: 1;
        width: 300px;
        height: 55px;
        background-color: transparent;
        border: 2px solid $white;
        color: $white;
        text-transform: uppercase;
        font-family: $proxima-bold;
        letter-spacing: 0.2em;
        transition: color .4s ease-in-out;
        @include r(1400) {
            width: 21.4285vw;
            height: 3.9285vw;
            font-size: 1vw; }
        @include r(500) {
            display: block;
            width: 60vw;
            height: 11vw;
            margin: auto;
            font-size: 2vw; } }
    &__btn:before {
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $white;
        transform: translate3d(0, 0, 0);
        transform-origin: 50% 100%;
        transform: scaleY(0);
        transition: transform .4s ease-in-out; }
    &__btn:hover:before {
        transform: scaleY(1); }
    &__btn:hover {
        color: $black; }
    &__line {
        position: absolute;
        top: 28px;
        left: 140px;
        width: 150px;
        height: 1px;
        background-color: $white;
        @include r(1400) {
            top: 2vw;
            left: 10vw;
            width: 10.7142vw;
            height: 1px; }
        @include r(500) {
            display: none; } }
    &__line-sm {
        position: absolute;
        top: 155px;
        left: 235px;
        width: 45px;
        height: 4px;
        background-color: $olive;
        @include r(1400) {
            top: 11.0714vw;
            left: 16.7857vw;
            width: 3.2142vw; }
        @include r(500) {
            display: none; } } }

