.header {
    position: relative;
    width: 100%;
    height: 100vh; }

.header-bg {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url('../img/header-bg.jpg');
    background-size: cover;
    background-position: center; }

.header-bg:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9); }

.menu-icon {
    cursor: pointer;
    position: absolute;
    z-index: 101;
    top: 34px;
    left: 27px;
    width: 33px;
    height: 22px;
    transform: translate3d(0, -300%, 0);
    @include r(1400) {
        top: 2.4285vw;
        left: 1.9285vw;
        width: 2.3571vw;
        height: 1.5714vw; }
    @include r(1000) {
        top: 3.4vw;
        left: 2.7vw;
        width: 3.3vw;
        height: 2.2vw; }
    @include r(500) {
        top: 6.8vw;
        left: 5.4vw;
        width: 6.6vw;
        height: 4.4vw; }
    &__item {
        position: absolute;
        width: 100%;
        height: 1px;
        background-color: $olive;
        &_top {
            top: 0; }
        &_center {
            top: 48%; }
        &_bottom {
            bottom: 0; } } }

.menu-label {
    display: block;
    position: absolute;
    z-index: 1;
    top: 35px;
    left: 74px;
    color: $grey;
    text-transform: uppercase;
    font-family: $proxima-bold;
    transform: translate3d(0, -300%, 0);
    @include r(1400) {
        top: 2.5vw;
        left: 5.2857vw;
        font-size: 1.0714vw; }
    @include r(1000) {
        top: 3.5vw;
        left: 7.4vw;
        font-size: 1.5vw; }
    @include r(500) {
       display: none; } }
.nav {
    position: absolute;
    z-index: 1;
    top: 22px;
    left: 30.315vw;
    display: flex;
    align-items: center;
    transform: translate3d(0, -200%, 0);
    @include r(1400) {
        top: 1.5714vw; }
    @include r(1000) {
        top: 2.2vw;
        left: 40vw; }
    @include r(500) {
        top: 4.8vw;
        left: 30vw; } }

.nav-logo {
    color: $white;
    text-decoration: none;
    text-transform: uppercase;
    font-family: $proxima-bold;
    font-size: 36px;
    letter-spacing: 10px;
    margin-right: 100px;
    @include r(1400) {
        font-size: 2.5714vw;
        letter-spacing: 0.714vw;
        margin-right: 7.1428vw; }
    @include r(1000) {
        font-size: 3.6vw;
        letter-spacing: 1vw; }
    @include r(500) {
        font-size: 7.2vw;
        letter-spacing: 2vw; } }

.nav-list {
    @include r(1000) {
        display: none; } }
.nav-item {
    float: left;
    margin-right: 100px;
    @include r(1400) {
        margin-right: 7.1428vw; } }

.nav-link {
    position: relative;
    color: $grey;
    text-transform: uppercase;
    font-family: $proxima-bold;
    padding: 5px 0;
    @include r(1400) {
        padding: 0.3571vw 0; } }

.nav-link:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-bottom: 8px solid rgba(255,255,255, 0.2);
    transform-origin: 100% 100%;
    transition: transform .3s ease-in-out;
    transform: scaleX(0);
    @include r(1400) {
        border-bottom: 0.5714vw solid rgba(255,255,255, 0.2); } }

.nav-link:hover:before {
    transform-origin: 0 100%;
    transform: scaleX(1); }


.info-label {
    z-index: 1;
    cursor: pointer;
    position: absolute;
    top: 33px;
    right: 114px;
    height: 25px;
    width: 25px;
    background-image: url('../img/info.png');
    background-position: center;
    background-size: cover;
    transform: translate3d(0, -300%, 0);
    @include r(1400) {
        top: 2.3571vw;
        right: 8.1428vw;
        height: 1.7857vw;
        width: 1.7857vw; }
    @include r(1000) {
        top: 3.3vw;
        right: 11.4vw;
        height: 2.5vw;
        width: 2.5vw; }
    @include r(500) {
        right: 5vw;
        top: 6.7vw;
        height: 5vw;
        width: 5vw; } }

.language-label {
    position: absolute;
    top: 35px;
    right: 33px;
    color: $grey;
    text-transform: uppercase;
    font-family: $proxima-bold;
    transform: translate3d(0, -300%, 0);
    @include r(1400) {
        top: 2.5vw;
        right: 2.3571vw; }
    @include r(1000) {
        top: 3.5vw;
        right: 3.3vw;
        font-size: 1.5vw; }
    @include r(500) {
        display: none;
        top: 7vw;
        font-size: 3vw; } }

.header-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center; }

.header-content {
    position: relative;
    padding-left: 30.315vw;
    @include r(500) {
        padding-left: 18vw; }
    &__title-roll {
        height: 120px;
        margin-bottom: 35px;
        overflow: hidden;
        @include r(1400) {
            height: 8.5714vw;
            margin-bottom: 2.5vw; }
        @include r(500) {
            height: 12vw;
            margin-bottom: 10vw; } }
    &__title {
        display: inline-block;
        color: $olive;
        text-transform: uppercase;
        font-family: $proxima-bold;
        font-size: 100px;
        letter-spacing: 0.2em;
        transform: translate3d(0, 100%, 0);
        @include r(1400) {
            font-size: 7.1428vw; }
        @include r(500) {
            font-size: 12vw; } }

    &__btn {
        position: relative;
        width: 300px;
        height: 55px;
        background-color: transparent;
        border: 2px solid $white;
        color: $white;
        text-transform: uppercase;
        font-family: $proxima-bold;
        letter-spacing: 0.15em;
        transition:  color .3s ease-in-out;
        opacity: 0;
        @include r(1400) {
            width: 21.4285vw;
            height: 3.9285vw;
            border: 0.1428vw solid $white;
            font-size: 1vw; }
        @include r(500) {
            margin-left: 12vw;
            width: 40vw;
            height: 7vw;
            border: 0.2vw solid $white;
            font-size: 2vw; } }
    &__btn:before {
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $white;
        border: 2px solid $white;
        transform: translate3d(0, 0, 0);
        transform-origin: 50% 100%;
        transform: scaleY(0);
        transition: transform .4s ease-in-out; }
    &__btn:hover:before {
        transform: scaleY(1); }
    &__btn:hover {
        color: $black; }
    &__icon-mouse {
        position: absolute;
        bottom: 191px;
        left: 30.315vw;
        width: 20px;
        height: 30px;
        background-image: url('../img/mouse-icon.png');
        background-size: cover;
        background-position: center;
        transform: translate3d(0, 100%, 0);
        opacity: 0;
        @include r(1400) {
            bottom: 13.6428vw;
            width: 1.4285vw;
            height: 2.1428vw; }
        @include r(1000) {
            bottom: 19.1vw;
            width: 2vw;
            height: 3vw; }
        @include r(500) {
            bottom: 38vw;
            width: 4vw;
            height: 6vw;
            left: 48vw; } }
    &__icon-arrow {
        position: absolute;
        bottom: 100px;
        height: 75px;
        width: 10px;
        left: 30.615vw;
        background-image: url('../img/arrow-icon.png');
        background-size: cover;
        background-position: center;
        transform: translate3d(0, 100%, 0);
        opacity: 0;
        @include r(1400) {
            bottom: 7.1428vw;
            height: 5.3571vw;
            width: 0.7142vw; }
        @include r(1000) {
            bottom: 10vw;
            height: 7.5vw;
            width: 1vw; }
        @include r(500) {
            bottom: 20vw;
            height: 15vw;
            width: 2vw;
            left: 49vw; } } }

.bottom-clip {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 40px;
    width: 55.42vw;
    background-color: $white;
    transform-origin: 0 100%;
    transform: scaleX(0);
    @include r(500) {
        display: none; } }

.pagination {
    position: fixed;
    z-index: 10;
    top: 50%;
    left: 28px;
    transform: translateY(-50%);
    @include r(1400) {
        left: 2vw; }
    @include r(500) {
        display: none; }
    &__line {
        height: 50px;
        width: 4px;
        background-color: $olive;
        margin: auto;
        margin-bottom: 18px;
        @include r(1400) {
            height: 3.5714vw;
            width: 0.25vw; } }
    &__number {
        line-height: 58px;
        display: block;
        color: $olive;
        transform-origin: 50% 50%;
        transform: rotate(90deg);
        font-size: 52px;
        font-family: $proxima-bold;
        margin-bottom: -17px;
        @include r(1400) {
            font-size: 3.7143vw;
            margin-bottom: -1.2142vw; } }
    &__dots {
        position: relative;
        margin-top: 80px;
        @include r(1400) {
            margin-top: 5.7142vw; } }

    &__dot {
        height: 10px;
        width: 10px;
        border-radius: 50%;
        background-color: #303030;
        margin: 22px auto;
        @include r(500) {
            height: 2vw;
            width: 2vw;
            margin: 4.4vw auto; } } }

.dot-active {
    position: absolute;
    height: 10px;
    width: 10px;
    border-radius: 50%;
    top: 0;
    left: 11px;
    background-color: $olive;
    @include r(1400) {
        left: 0.77vw; }
    @include r(1000) {
        left: 0.6vw; }
    @include r(800) {
        left: 0.4vw; }
    @include r(700) {
        left: 0.5vw; }
    @include r(500) {
        height: 2vw;
        width: 2vw;
        left: 0.2vw;
        top: -0.3vw; } }










