.mobile {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: -100vh;
    left: 0;
    background-color: $black;
    z-index: 100; }

.nav-mobile {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    &__item {
        padding: 10px 0; }
    &__link {
        display: block;
        color: $white;
        font-size: 62px;
        font-family: $proxima-bold;
        text-transform: uppercase;
        transform: translate3d(0, 0, 0);
        transition: transform .3s ease-out;
        @include r(1000) {
            font-size: 6.2vw; } }
    &__link:hover {
        transform: translate3d(1vw, 0, 0); } }

.close-icon {
    position: absolute;
    top: 33px;
    left: 33px;
    cursor: pointer;
    width: 30px;
    height: 30px;
    transform: scale(0);
    @include r(1000) {
        top: 3.3vw;
        left: 3.3vw;
        width: 3vw;
        height: 3vw; }
    @include r(500) {
        top: 6.6vw;
        left: 6.6vw;
        width: 6vw;
        height: 6vw; }
    &__item {
        width: 30px;
        height: 1px;
        background-color: $white;
        transform-origin: 50% 50%;
        @include r(1000) {
            width: 3vw; }
        @include r(500) {
            width: 6vw; }
        &_right {
            transform: rotate(45deg); }
        &_left {
            transform: rotate(-45deg);
            position: relative;
            top: -1px; } } }
