body {
	font-family: $proxima-regular;
	font-size: 15px;
	@include r(1400) {
		font-size: 1.0714vw; } }

a {
	text-decoration: none;
	color: inherit; }

body {
	overflow: auto; }

.app {
	position: fixed;
	width: 100%;
	height: 100vh;
	overflow: auto;
	@include r(500); }

.scrollbar-thumb {
	background-color: $black !important; }

.loader {
	z-index: 100; }

.active-label {
	display: block !important; }

.change {
	transform-origin: 100% 0 !important; }

