.about {
    padding: 130px 0;
    overflow: hidden;
    @include r(1400) {
        padding: 9.28vw 0; }
    @include r(500) {
        padding: 26vw 0; } }
.about-content {
    width: 1200px;
    margin: auto;
    display: flex;
    align-items: center;
    @include r(1400) {
        width: 85.7142vw; }
    @include r(500) {
        display: block;
        width: 90vw;
        margin: auto; } }
.about-image {
    width: 588px;
    height: 400px;
    background-size: cover;
    background-position: center;
    @include r(1400) {
        width: 42vw;
        height: 28.5714vw; }
    @include r(500) {
        width: 100%;
        height: 80vw;
        margin-bottom: 10vw; } }
.about-description {
    padding-left: 100px;
    width: 480px;
    position: relative;
    @include r(1400) {
        padding-left: 7.0428vw;
        width: 34.2857vw; }
    @include r(500) {
        padding-left: 16vw;
        width: 96vw; }
    &__title {
        font-size: 48px;
        font-family: $proxima-bold;
        text-transform: uppercase;
        letter-spacing: 0.07em;
        margin-bottom: 20px;
        @include r(1400) {
            font-size: 3.4285vw;
            margin-bottom: 1.4285vw; }
        @include r(500) {
            font-size: 9.6vw; } }
    &__description {
        line-height: 25px;
        color: $dark-grey;
        margin-bottom: 20px;
        @include r(1400) {
            line-height: 1.7857vw;
            margin-bottom: 1.4285vw; }
        @include r(500) {
            line-height: 5vw;
            font-size: 3vw;
            width: 50vw; } }
    &__link {
        position: relative;
        font-family: $proxima-bold;
        text-transform: uppercase;
        display: flex;
        align-items: center; }
    &__label {
        margin-right: 10px;
        @include r(1400) {
            margin-right: 0.7142vw; }
        @include r(500) {
            font-size: 3vw;
            margin-right: 2vw; } }
    &__link:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-bottom: 2px solid $olive;
        transform-origin: 0 100%;
        transform: scaleX(0);
        transition: transform .7s cubic-bezier(.18,.44,.45,.89); }
    &__link:hover:before {
        transform: scaleX(0.1); }
    &__arrow {
        background-image: url('../img/arrow-right.png');
        width: 35px;
        height: 7px;
        background-size: cover;
        transition: transform .7s cubic-bezier(.18,.44,.45,.89);
        transform: translateX(0);
        @include r(1400) {
            width: 2.5vw;
            height: 0.5vw; }
        @include r(500) {
            width: 7vw;
            height: 1.4vw; } }
    &__link:hover &__arrow {
        transform: translateX(50%); }
    &__line {
        position: absolute;
        top: 25px;
        left: -85px;
        width: 150px;
        height: 1px;
        background-color: $black;
        @include r(1400) {
            top: 1.7857vw;
            left: -6.0714vw;
            width: 10.7142vw; }
        @include r(500) {
            top: 5vw;
            left: 1vw;
            width: 11vw; } }

    &__line-sm {
        position: absolute;
        top: 88px;
        left: 16px;
        width: 45px;
        height: 4px;
        background-color: $olive;
        @include r(1400) {
            top: 6.2857vw;
            left: 1.1428vw;
            width: 3.2142vw;
            height: 0.2857vw; }
        @include r(500) {
            top: 15.6vw;
            left: 8vw; } } }


