.products {
    padding-top: 90px;
    overflow: hidden;
    background-color: $white;
    position: relative;
    z-index: 1;
    overflow-x: hidden;
    @include r(1400) {
        padding-top: 6.4285vw; }
    @include r(500) {
        padding: 18vw 0; } }

.product {
    position: relative; }

.products-content {
    width: 1200px;
    margin: auto;
    @include r(1400) {
        width: 85.7143vw; } }

.product-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 250px;
    @include r(1400) {
        padding-bottom: 17.8571vw; }
    @include r(500) {
        display: block; } }


.product-description {
    position: relative;
    padding-left: 180px;
    width: 48%;
    @include r(1400) {
        padding-left: 12.8571vw; }
    @include r(500) {
        width: 100%;
        margin-bottom: 10vw; }
    &__title {
        font-family: $proxima-bold;
        font-size: 48px;
        text-transform: uppercase;
        letter-spacing: 0.05em;
        line-height: 100px;
        @include r(1400) {
            font-size: 3.4285vw;
            line-height: 7.1428vw; }
        @include r(500) {
            font-size: 9.6vw;
            line-height: 20vw; } }
    &__description {
        color: $dark-grey;
        line-height: 25px;
        margin-bottom: 20px;
        @include r(1400) {
            line-height: 1.7857vw;
            margin-bottom: 1.4285vw; }
        @include r(500) {
            line-height: 5vw;
            font-size: 3vw; } }

    &__link {
        display: flex;
        align-items: center; }

    &__link-label {
        position: relative;
        text-transform: uppercase;
        font-family: $proxima-bold;
        margin-right: 1em;
        padding-bottom: 3px;
        @include r(1400) {
            padding-bottom: 0.2142vw; }
        @include r(500) {
            //padding-bottom: 6vw
            font-size: 3vw; } }
    &__arrow {
        width: 35px;
        height: 7px;
        background-image: url(../img/arrow-right.png);
        background-size: cover;
        transition: transform .8s cubic-bezier(.18,.44,.45,.89);
        @include r(1400) {
            width: 2.5vw;
            height: 0.5vw; }
        @include r(500) {
            width: 7vw;
            height: 1.4vw; } }
    &__link-label:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-bottom: 2px solid $olive;
        transform-origin: 0 100%;
        transform: scaleX(0);
        transition: transform .7s cubic-bezier(.18,.44,.45,.89); }
    &__link-label:hover:before {
        transform: scaleX(0.4); }
    &__link-label:hover~ &__arrow {
        transform: translate3d(50%, 0, 0); }
    &__line-lg {
        position: absolute;
        top: 46px;
        left: 0;
        width: 150px;
        height: 1px;
        background-color: $black;
        @include r(1400) {
            top: 3.2857vw;
            width: 10.7142vw;
            height: 0.07142vw; }
        @include r(500) {
            top: 9.2vw;
            height: 1px; } }
    &__line-sm {
        position: absolute;
        top: 110px;
        left: 100px;
        height: 3px;
        width: 45px;
        background-color: $olive;
        @include r(1400) {
            top: 7.8571vw;
            left: 7.1428vw;
            height: 0.2142vw;
            width: 3.2142vw; }
        @include r(500) {
            top: 22vw; } } }

.product-image {
    width: 48%;
    height: 400px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    @include r(1400) {
        height: 28.5714vw; }
    @include r(500) {
        height: 80vw;
        width: 100%; } }


.product-number {
    position: absolute;
    top: 365px;
    @include r(1400) {
        top: 26.0714vw; }
    @include r(500) {
        top: 30vw; }
    &__label {
        display: inline-block;
        font-family: $proxima-bold;
        font-size: 36px;
        margin-bottom: 80px;
        @include r(1400) {
            font-size: 2.5714vw;
            margin-bottom: 5.7142vw; } }
    &__line {
        height: 50px;
        width: 4px;
        background-color: $olive;
        margin: auto;
        @include r(1400) {
            height: 3.5714vw;
            width: 0.25vw; } } }

.product:nth-child(even) {
    & .product-wrapper {
        display: flex;
        flex-direction: row-reverse;
        @include r(500) {
            display: block; } }
    & .product-number {
        @include r(1920) {
            right: 14px !important; }
        @include r(500) {
            right: 92% !important;
            display: inline-block; } } }

.product:nth-child(odd) {
    & .product-number {
        left: 14px; } }

.product:nth-child(even) {
     & .overlay-label {
        left: -10%;
        top: 70%; } }


.overlay-label {
    position: absolute;
    top: 30%;
    left: 60%;
    font-size: 170px;
    color: #d0d0d0;
    text-transform: uppercase;
    opacity: 0.4;
    font-family: $proxima-bold; }


