.loader {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $black;
    width: 100%;
    height: 100vh;
    &__title {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: $white;
        font-size: 50px;
        text-transform: uppercase;
        font-family: $proxima-bold;
        letter-spacing: 0.1em;
        display: none; }
    &__overlay {
        height: 55px;
        width: 270px;
        background-color: $white;
        transform-origin: 0 100%;
        transform: scaleX(0); } }
