.footer {
    height: 68px;
    width: 100%;
    background-color: $black;
    overflow: hidden;
    @include r(1400) {
        height: 4.8175vw; }
    @include r(500) {
        height: 13.6vw; }
    &__rights {
        color: $dark-grey;
        display: block;
        @include r(1400) {
            margin-right: 28.5714vw; }
        @include r(500) {
            margin-right: 20vw; } }
    &__logo {
        font-family: $proxima-bold;
        font-size: 30px;
        color: $white;
        letter-spacing: 0.2em;
        text-transform: uppercase;
        @include r(1400) {
            margin-right: 18.8142vw;
            font-size: 2.1428vw; }
        @include r(500) {
            font-size: 6vw; } }
    &__by {
        &_grey {
            color: $dark-grey;
            margin-right: 0.7em; }
        &_white {
            color: $white; }
        @include r(500) {
            display: none; } } }

.footer-wrapper {
    width: 1200px;
    height: 100%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include r(1400) {
        width: 85.7142vw;
        justify-content: flex-start; } }

