.quality {
    padding-bottom: 150px;
    @include r(1400) {
        padding-bottom: 10.7142vw; }
    @include r(500) {
        padding-bottom: 30vw; }
    &__title {
        text-align: center;
        font-family: $proxima-bold;
        font-size: 48px;
        text-transform: uppercase;
        letter-spacing: 0.08em;
        line-height: 100px;
        @include r(1400) {
            font-size: 3.4285vw;
            line-height: 7.1428vw; }
        @include r(500) {
            font-size: 9.6vw;
            line-height: 13vw; } }
    &__line {
        height: 4px;
        width: 44px;
        background-color: $olive;
        margin: auto;
        margin-bottom: 20px;
        @include r(1400) {
            height: 0.25vw;
            width: 3.1428vw;
            margin-bottom: 1.4285vw; }
        @include r(500) {
            height: 0.8vw;
            width: 8.8vw;
            margin-top: 4vw;
            margin-bottom: 4vw; } }
    &__description {
        width: 550px;
        text-align: center;
        margin: auto;
        color: $dark-grey;
        line-height: 25px;
        margin-bottom: 60px;
        @include r(1400) {
            width: 39.2857vw;
            line-height: 1.7857vw; }
        @include r(500) {
            font-size: 3vw;
            line-height: 5vw;
            width: 70%; } } }

.quality-video {
    width: 1000px;
    margin: auto;
    @include r(1400) {
        width: 71.4285vw; } }

.quality-video-list {
    white-space: nowrap; }

.quality-video-item {
    display: inline-block;
    padding: 0 30px;
    @include r(1400) {
        padding: 0 2.1428vw; }
    @include r(500) {
        display: block;
        margin-bottom: 12vw; }
    &__border {
        height: 270px;
        width: 270px;
        border: 1px solid $light-grey;
        border-radius: 50%;
        margin-bottom: 25px;
        @include r(1400) {
            height: 19.2875vw;
            width: 19.2875vw;
            margin-bottom: 1.7857vw;
            border: 0.0714vw solid $light-grey; }
        @include r(500) {
            margin: auto;
            height: 54vw;
            width: 54vw;
            margin-bottom: 5vw;
            border: 1px solid $light-grey; } }
    &__wrapper {
        padding: 15px;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        @include r(1400) {
            padding: 1.0714vw; }
        @include r(500) {
            padding: 3vw; } }
    &__bg {
        position: relative;
        width: 100%;
        height: 100%;
        border-radius: 50%; }
    &__btn {
        cursor: pointer;
        position: absolute;
        z-index: 1;
        width: 70px;
        height: 70px;
        top: 83px;
        left: 85px;
        background-image: url('../img/play-btn.png');
        background-size: cover;
        background-repeat: no-repeat;
        transform-origin: 50% 50%;
        transition: transform .3s ease-in-out;
        @include r(1400) {
            width: 5vw;
            height: 5vw;
            top: 5.9285vw;
            left: 6.0714vw; }
        @include r(500) {
            width: 14vw;
            height: 14vw;
            top: 16.6vw;
            left: 17vw; } }
    &__btn:hover {
        transform: scale(1.1); }
    &__line {
        width: 25px;
        height: 1px;
        background-color: $olive;
        margin: auto;
        margin-bottom: 15px;
        @include r(1400) {
            width: 1.7857vw;
            margin-bottom: 1.0714vw; }
        @include r(500) {
            width: 5vw;
            height: 1px;
            margin-bottom: 3vw; } }
    &__label {
        font-size: 16px;
        display: block;
        text-align: center;
        @include r(1400) {
            font-size: 1.1428vw; }
        @include r(500) {
            font-size: 3.2vw; } } }



